.App {
  text-align: center;

  /*background-size: cover;*/
  /*max-height: 100%;*/
}

.home {
  box-sizing: border-box;
  width: 100vw;
  height: 90vh;
  background-image: url("./img/bg3.gif");
}

.text3d-wrap-3 {
  margin: 0;
  padding: 0;
  /*background-color: #337AB7;*/
  position: relative;
  width: 100%;
  height: 25vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.text3d-3 {
  margin-top: -12px;
  align-self: center;
  transform: perspective(300px) rotateX(15deg) scaleY(0.8);
  transition: all 0.5s;
  font-family: Impact, Charcoal, sans-serif;
  font-size: 90px;
  letter-spacing: 3px;
  color: #FFF;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.9), 0 10px 0 #BFE2FF;
}
.text3d-wrap-3:hover .text3d-3 {
  transform: perspective(1000px) rotateX(15deg) scale(1.07);
  text-shadow: 0 0 10px black, 0 2px 0 #BFE2FF;
}
@media (max-width:767px) {
  .text3d-3 {
    font-size: 60px;
  }
}

.navbar {
  height: 10vh;
  color: #61dafb;
  width: 100vw;
  display: flex;
  align-items: center;
  padding: 0 15px;
  background: #5a77f8;
}

.navbar__links {
  margin-right: auto;
  color: blue;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.empty {
  width: 100vw;
  height: 100vh;
  background-image: url("./img/bg3.gif");
}
